import React, { Component } from 'react';
import { Form, Select, DatePicker, Button, Radio, Input, Table, Modal, Row, Col, Tabs, message } from 'antd';
import { ColumnProps, CompareFn, TableEventListeners } from 'antd/lib/table';
import { RadioChangeEvent } from 'antd/lib/radio/interface';
import monent from 'moment';

import request from '@/request';

import './Info.less';

interface IProps {
  form: any;
}
interface List {
  vehicleId: string;
  plateNumber: string;
  commitTime: string;
  vehicleType: string;
  crimeType: string;
  direction: string;
  imagePath: string[];
  videoPath: string;
  location: string;
  valid: number;
}
interface IState {
  loading: boolean;
  modalVisible: boolean;
  currentItem: List;
  data: List[];
  totalCount: number;
  conditions: {
    crimeType: string[];
    vehicleType: string[];
    crimeLocation: string[];
  };
  filter: {
    curPage: number;
    size: number;
    orderBy: string;
    crimeType: string | number;
    vehicleType: string | number;
    location: string | number;
    timeFrom: number;
    timeTo: number;
  };
}

class IllegalInfo extends Component<IProps, IState> {
  public state = {
    loading: false,
    modalVisible: false,
    currentItem: {} as List,
    data: [] as List[],
    totalCount: 0,
    conditions: {
      crimeType: [],
      vehicleType: [],
      crimeLocation: []
    },
    filter: {
      curPage: 1,
      size: 10,
      orderBy: 'plateNumber',
      sequence:'desc',
      crimeType: 'all',
      vehicleType: 'all',
      location: 'all',
      timeFrom: 0,
      timeTo: Date.now()
    }
  };
  public componentDidMount() {
    this.getOptions();
    this.getData();
  }
  getOptions() {
    request
      .config({
        url: `/vehicle/conditions`
      })
      .then((res: any) => {
        this.setState({ conditions: res.data });
      });
  }
  /**
   * getData
   */
  public getData() {
    const { filter } = this.state;
    this.setState({
      loading: true
    });
    request
      .config({
        url: `/vehicle/curPage/${filter.curPage}/size/${filter.size}/orderBy/${filter.orderBy}/sequence/${filter.sequence}/crimeType/${filter.crimeType}/vehicleType/${
          filter.vehicleType
        }/location/${filter.location}/timeFrom/${filter.timeFrom}/timeTo/${filter.timeTo}`
      })
      .then((res: any) => {
        if (res.code === 0) {
          this.setState({ totalCount: res.data.totalCount, data: res.data.list });
        }
      })
      .finally(() => {
        this.setState({
          loading: false
        });
      });
  }
  /**
   * handleSubmit
   */
  public handleSubmit = () => {
    const { getFieldsValue } = this.props.form;
    const { filter } = this.state;
    let data = getFieldsValue();
    if (data.date && data.date.length > 1) {
      data.timeFrom = data.date[0].valueOf();
      data.timeTo = data.date[1].valueOf();
    }
    this.setState(
      {
        filter: {
          ...filter,
          ...data
        }
      },
      () => {
        this.getData();
      }
    );
  };
  /**
   * handleOnRow
   */
  public handleOnRow = (record: List): TableEventListeners => {
    return {
      onClick: () => {
        console.log(record);
        this.setState({
          modalVisible: true,
          currentItem: record
        });
      }
    };
  };
  /**
   * tableFooter
   */
  public tableFooter = (currentPageData: object[]): React.ReactNode => {
    return (
      <span>
        :{currentPageData.length} :{currentPageData.length} :0 :0
      </span>
    );
  };
  /**
   * handleCrrentChange
   */
  public handleCrrentChange = (name: string, e: RadioChangeEvent | React.ChangeEvent) => {
    const { currentItem } = this.state;
    this.setState({
      currentItem: {
        ...currentItem,
        [name]: (e.target as any).value
      }
    });
  };
  public handleSubmitCurrent = () => {
    const { currentItem } = this.state;
    request
      .config({
        url: `/vehicle/detail`,
        type: 'put',
        data: currentItem
      })
      .then((res: any) => {
        if (res.code === 0) {
          message.info('!');
          this.getData();
        } else {
          message.info(res.message);
        }
      });
  };
  /**
   * handleModalCancel
   */
  public handleModalCancel = () => {
    this.setState({
      modalVisible: false,
      currentItem: {} as List
    });
  };
  public tableOnchange = (pagination:any, filters:any, sorter:any) => {
    let { filter } = this.state;
    filter.sequence = sorter.order == 'ascend'?'asc':'desc';
    filter.curPage = pagination.current;
    this.setState({filter},()=>{this.getData()});
  };
  public pageChange = (page:number) => {
      let { filter } = this.state;
      filter.curPage = page;
      this.setState({filter},()=>{this.getData()});
  };

  public render() {
    const { getFieldDecorator } = this.props.form;
    const { loading, modalVisible, data, totalCount, currentItem, filter, conditions } = this.state;
    const formItemLayout = {
      labelCol: {
        sm: { span: 6 }
      },
      wrapperCol: {
        sm: { span: 18 }
      }
    };
    const columns: ColumnProps<List>[] = [
      {
        title: '',
        dataIndex: 'plateNumber',
        key: 'plateNumber',
        render(text: string, record:any) {
            return <span className={"carNum carColor"+`${record.plateColor}`}>{text}</span>;
        }
      },
      {
        title: '',
        dataIndex: 'vehicleType',
        key: 'vehicleType'
      },
      {
        title: '',
        dataIndex: 'crimeType',
        key: 'crimeType'
      },
      {
        title: '',
        dataIndex: 'direction',
        key: 'direction'
      },
      {//''
        title: ()=>{
          return <div></div>;
        },
        dataIndex: 'commitTime',
        key: 'commitTime',
        // sorter: ((a: any, b: any) => a.time - b.time) as CompareFn<List>,
        sorter:true,
        render(text: number) {
          return monent(text * 1000).format('YYYY-MM-DD HH:mm:ss');
        }
      }
    ];
    return (
      <div className="illegal-info">
        <Form {...formItemLayout}>
          <Row>
            <Col span={8}>
              <Col span={18} offset={3}>
                <Form.Item label=":">
                  {getFieldDecorator('crimeType', {
                    initialValue: filter.crimeType
                  })(
                    <Select placeholder="" showSearch>
                      <Select.Option value="all"></Select.Option>
                      {conditions.crimeType &&
                        conditions.crimeType.map((x, y) => (
                          <Select.Option key={y} value={x}>
                            {x}
                          </Select.Option>
                        ))}
                    </Select>
                  )}
                  {/* <span className="ant-form-text" style={{ color: 'red' }}>
                    
                  </span> */}
                </Form.Item>
              </Col>
              <Col span={18} offset={3}>
                <Form.Item label=":">
                  {getFieldDecorator('vehicleType', {
                    initialValue: filter.vehicleType
                  })(
                    <Select placeholder="" showSearch>
                      <Select.Option value="all"></Select.Option>
                      {conditions.vehicleType &&
                        conditions.vehicleType.map((x, y) => (
                          <Select.Option key={y} value={x}>
                            {x}
                          </Select.Option>
                        ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </Col>
            <Col span={8}>
              <Col span={18} offset={3}>
                <Form.Item label=":">{getFieldDecorator('date')(<DatePicker.RangePicker />)}</Form.Item>
              </Col>
              <Col span={18} offset={3}>
                <Form.Item label=":">
                  {getFieldDecorator('location', {
                    initialValue: filter.location
                  })(
                    <Select placeholder="" showSearch>
                      <Select.Option value="all"></Select.Option>
                      {conditions.crimeLocation &&
                        conditions.crimeLocation.map((x, y) => (
                          <Select.Option key={y} value={x}>
                            {x}
                          </Select.Option>
                        ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </Col>
          </Row>
          <Row>
            <Col span={8}>
              <Col span={18} offset={3}>
                <Form.Item
                  wrapperCol={{
                    sm: { span: 18, offset: 6 }
                  }}
                >
                  <Button type="primary" onClick={this.handleSubmit}>
                    
                  </Button>
                </Form.Item>
              </Col>
            </Col>
          </Row>
        </Form>
        <Table<List>
          loading={loading}
          rowKey={(record: List) => record.vehicleId}
          columns={columns}
          dataSource={data}
          pagination={{ showTotal: total => `: ${total} `, defaultCurrent: filter.curPage, total: totalCount} }
          // scroll={{ y: 'calc(100vh - 588px)' }}
          onRow={this.handleOnRow}
          onChange={this.tableOnchange}
        />

        {modalVisible && (
          <Modal className="illegal-info-modal" title="" visible={modalVisible} onCancel={this.handleModalCancel} footer={null} width="70%">
            <div className="info">
              <span className="text">:</span> {currentItem.crimeType}-{currentItem.plateNumber} <span className="text">:</span>{' '}
              {monent(currentItem.commitTime).format('YYYY-MM-DD HH:mm:ss')} <span className="text">:</span> {currentItem.location}
            </div>
            <Row gutter={16}>
              <Col span={8}>
                <Form>
                  <Form.Item label=":">
                    <Radio.Group defaultValue={currentItem.valid} onChange={this.handleCrrentChange.bind(this, 'valid')}>
                      <Radio value={0}></Radio>
                      <Radio value={1}></Radio>
                      <Radio value={3}></Radio>
                    </Radio.Group>
                  </Form.Item>
                  {currentItem.valid === 3 && (
                    <Form.Item label=":">
                      <Input value={currentItem.plateNumber} onChange={this.handleCrrentChange.bind(this, 'plateNumber')} allowClear={true} style={{ width: 200 }} />
                    </Form.Item>
                  )}
                  <Form.Item style={{ padding: '10px 0' }}>
                    <Button type="primary" onClick={this.handleSubmitCurrent}>
                      
                    </Button>
                  </Form.Item>
                </Form>
              </Col>
              <Col span={16}>
                <Tabs>
                  <Tabs.TabPane tab="" key="1">
                    <div className="img-or-pic">{currentItem.imagePath && currentItem.imagePath.map((x, y) => <img key={y} src={`${request.baseUrl}${x}`} alt="" />)}</div>
                  </Tabs.TabPane>
                  <Tabs.TabPane tab="" key="2">
                    <div className="img-or-video">
                      <video src={`${request.baseUrl}${currentItem.videoPath}`} autoPlay={true} controls={true} />
                    </div>
                  </Tabs.TabPane>
                </Tabs>
              </Col>
            </Row>
          </Modal>
        )}
      </div>
    );
  }
}

export default Form.create({ name: 'IllegalInfo' })(IllegalInfo);
