// import qs from 'qs';
import axios, { Method } from 'axios';
import Cookies from 'js-cookie';
// axios.defaults.baseURL = process.env.NODE_ENV === 'development' ? '/api' : 'http://139.159.221.224:28080/api';
axios.defaults.baseURL = '/api';

interface IOptions {
  url: string,
  params: any,
  data: object | null,
  headers: any,
  type: Method
}

class Request {
  public baseUrl = '';//http://139.159.221.224:28080
  public options: IOptions = {
    url: '',
    params: null,
    data: null,
    headers: {},
    type: 'get'
  }
  public getUrlParams(data: any) {
    let params = data || {};
    let token = Cookies.get('token');
    if (token) {
      params.token = token;
    }
    return {
      ...params,
      'Content-Type': 'application/json'
    };
  }
  public config(obj: any) {
    this.options = obj;
    return this.run();
  }
  public setConfig(type: any) {
    return {
      headers: this.getUrlParams(this.options.headers),
      method: type || this.options.type,
      url: `${this.options.url}?_=${Date.now()}`,
      params: this.options.params || {}, // Get
      data: this.options.data || {} // Post
    };
  }
  public run() {
    return new Promise((resolve, reject) => {
      if (navigator.onLine) {
        axios
          .request({
            withCredentials: true,
            headers: this.getUrlParams(this.options.headers),
            method: this.options.type,
            url: `${this.options.url}?_=${Date.now()}`,
            params: this.options.params || {}, // Get
            data: this.options.data || {} // Post
          })
          .then(res => {
            let data = res.data;
            if (data.state === 501 || data.state === 502) {
              Cookies.remove('token');
              Cookies.remove('userId');
              // if (!this.options.redirect) {
              //   if (data.state === 502) {
              //     Toast(data.message);
              //   }
              //   let redirectUrl = tools.getUrlParams('redirectUrl');
              //   return setTimeout(() => {
              //     Loading.close();
              //     window.location.href = `/login?redirectUrl=${redirectUrl ? redirectUrl : encodeURIComponent(window.location.href)}`;
              //   }, 2000);
              // }
            }
            resolve(data);
          })
          .catch(res => {
            reject(res);
          });
      } else {
        // Toast('');
        reject({ state: 404, message: '' });
      }
    });
  }
}
export default new Request();